<template>
  <div>
    <h2>我是About的内容</h2>
  </div>
</template>

<script>
export default {
  name: "About",
  data() {
    return {};
  },
  // 进入
  // 路由守卫--他和前置和后置没关系 不是前置和后置
  //通过路由规则，进入该组件时被调用
  beforeRouteEnter: (to, from, next) => {
    if (to.meta.isAuth) {
      //判断是否需要鉴权
      if (localStorage.getItem("school") === "atguigu") {
        next();
      } else {
        alert("学校名不对 无权限查看");
      }
    } else {
      next();
    }
  },
  // 离开
  //通过路由规则，离开该组件时被调用
  beforeRouteLeave(to, from, next) {
    next();//放行
  },
};
</script>

