<template>
  <div>
    <ul>
      <li v-for="m in messageList" :key="m.id">
        <router-link
          :to="{
            name: 'xiangqing',
            query: { id: m.id, title: m.title },
          }"
        >
          {{ m.title }}
        </router-link>

        <!-- 按钮 -->
        <button @click="pushShow(m)">push查看</button>
        <button @click="replaceShow(m)">replace查看</button>
      </li>
    </ul>
    <hr />

    <!-- 路由视图 -->
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "Message",
  data() {
    return {
      messageList: [
        { id: "001", title: "消息1" },
        { id: "002", title: "消息2" },
        { id: "003", title: "消息3" },
      ],
    };
  },
  methods: {
    pushShow(m) {
      this.$router.push({
        name: "xiangqing",
        query: { id: m.id, title: m.title },
      });
    },
    replaceShow(m){
      this.$router.replace({
        name: "xiangqing",
        query: { id: m.id, title: m.title },
      });
    }
  },
};
</script>

