//引入Vue
import Vue from 'vue'
//引入App
import App from './App.vue'
//关闭Vue的生产提示
Vue.config.productionTip = false
//  引入路由
import VueRouter from 'vue-router'
//  使用路由
Vue.use(VueRouter)
// 引入路由器
import router from './router/index'

//创建vm
new Vue({
	el: '#app',
	render: h => h(App),
	// 路由器
	//  router:router 简写
	router
})